const currencyFormat = (currency: string) => {
    const premap = currencyMap[currency];
    if (premap) return premap;
    if (currency.toLowerCase().includes('usd')) return '$' + currency.replace('USD', '');
    if (currency.toLowerCase().includes('eur')) return '€' + currency.replace('EUR', '');
    if (currency.toLowerCase().includes('gbp')) return '£' + currency.replace('GBP', '');
    if (currency.toLowerCase().includes('jpy')) return '¥' + currency.replace('JPY', '');
    return currency;
}

const DataChart = ({ bar, noScale, mainColor, label, dataKey, data, width, scaleDir, sync }: any) => {
    const chartContainerRef = React.useRef(null as any) as any;
    React.useEffect(() => {
        const handleResize = () => {
            chart.applyOptions({ width: chartContainerRef.current.clientWidth });
        };

        const toopTipHandler = (chart: any, series: any, label: any) => {


            const toolTipWidth = 80;
            const toolTipHeight = 80;
            const toolTipMargin = 15;

            let toolTip = document.createElement('div') as any;
            toolTip.style = `width: 168px; font-weight:600; height: 80px; position: absolute; display: none; padding: 8px; box-sizing: border-box; font-size: 12px; text-align: left; z-index: 1000; top: 12px; left: 12px; pointer-events: none; border: 1px solid; border-radius: 0.1em;font-family: -apple-system, BlinkMacSystemFont, 'Trebuchet MS', Roboto, Ubuntu, sans-serif; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; background: #ffffff00; color: #000000; border-color: #2962FF; backdrop-filter: blur(10px); -webkit-backdrop-filter: blur(10px);`;
            //toolTip.style = `position: absolute;`
            chartContainerRef.current.appendChild(toolTip);

            chart.subscribeCrosshairMove((param: any) => {
                if (
                    param.point === undefined ||
                    !param.time ||
                    param.point.x < 0 ||
                    param.point.x > chartContainerRef.current.clientWidth ||
                    param.point.y < 0 ||
                    param.point.y > chartContainerRef.current.clientHeight
                ) {
                    toolTip.style.display = 'none';
                } else {
                    // time will be in the same format that we supplied to setData.
                    // thus it will be YYYY-MM-DD
                    const dateStr = new Date(param.time).toLocaleString();
                    toolTip.style.display = 'block';
                    const data = param.seriesData.get(series);
                    const price = data.value !== undefined ? data.value : data.close;
                    toolTip.style.borderColor = price > 0 ? 'green' : 'red';

                    toolTip.innerHTML = `
                <div style="display:flex;justify-content:center;align-items:center;color: ${toolTip.style.borderColor}">${label}</div>
                <div style="display:flex;justify-content:center;align-items:center;font-size: 24px; margin: 4px 0px; color: ${'black'}"> ${Math.round(100 * price) / 100} </div>
                <div style="display:flex;justify-content:center;align-items:center;color: ${'black'}"> ${dateStr} </div>`;
                    const coordinate = series.priceToCoordinate(price);
                    let shiftedCoordinate = param.point.x - 50;
                    if (coordinate === null) {
                        return;
                    }
                    shiftedCoordinate = Math.max(
                        0,
                        Math.min(chartContainerRef.current.clientWidth - toolTipWidth, shiftedCoordinate)
                    );
                    const coordinateY =
                        coordinate - toolTipHeight - toolTipMargin > 0
                            ? coordinate - toolTipHeight - toolTipMargin
                            : Math.max(
                                0,
                                Math.min(
                                    chartContainerRef.current.clientHeight - toolTipHeight - toolTipMargin,
                                    coordinate + toolTipMargin
                                )
                            );
                    toolTip.style.left = shiftedCoordinate + 'px';
                    toolTip.style.top = coordinateY + 'px';
                }
            });
        }
        const chart = createChart(chartContainerRef.current, {
            width: chartContainerRef.current.clientWidth,
            height: 300,
            watermark: {
                visible: true,
                text: label,
                fontSize: 24,
                color: theme.palette.neutralQuaternary,
                fontStyle: 'bold',
                horzAlign: 'left',
                vertAlign: 'top',
            },
            grid: {
                vertLines: {
                    color: 'rgba(197, 203, 206, 0)',
                },
                horzLines: {
                    color: 'rgba(197, 203, 206, 0)',
                },
            },
            timeScale: {
                visible: false,
            },
            leftPriceScale: {
                visible: false,
            },
            rightPriceScale: {
                visible: false,
            },
        });
        let series = null as any;
        chart.timeScale().fitContent();
        if (bar) {
            series = chart.addHistogramSeries({
                color: mainColor,
                base: 0,
                ...(!noScale ? { scaleMargins: { top: 0, bottom: 0 } } : {}),
                ...(scaleDir === 'right' ? { priceScaleId: 'right' } : { priceScaleId: 'left' }),
            });
            series.setData(data.map((d: any) => ({ time: d.time, value: d[dataKey] })).sort((a: any, b: any) => a.time - b.time).filter((d: any) => d.value).map((d: any) => ({ ...d, color: d.value > 0 ? 'rgba(16, 124, 16, 1)' : 'rgba(232, 17, 35, 1)' })))
        }
        if (!bar) {
            series = chart.addBaselineSeries({
                topFillColor2: 'rgba(16, 124, 16, 0.1)',
                topFillColor1: 'rgba(16, 124, 16, 1)',
                topLineColor: 'rgba(16, 124, 16, 1)',
                bottomFillColor1: 'rgba(232, 17, 35, 0.1)',
                bottomFillColor2: 'rgba(232, 17, 35, 1)',
                bottomLineColor: 'rgba(232, 17, 35, 1)',
                lineWidth: 1,
                lineType: 2,
                ...(noScale ? { scaleMargins: { top: 0, bottom: 0 } } : {}),
                ...(scaleDir === 'right' ? { priceScaleId: 'right' } : { priceScaleId: 'left' }),
            });
            series.setData(data.map((d: any) => ({ time: d.time, value: d[dataKey] })).sort((a: any, b: any) => a.time - b.time).filter((d: any) => d.value));
        }
        toopTipHandler(chart, series, label);
        window.addEventListener('resize', handleResize);

        return () => {
            window.removeEventListener('resize', handleResize);
            chart.remove();
        };
    }, [data])
    return (<>
        <div style={{
            width: width - 2 + '%', height: '100%',
            border: '1px solid ' + theme.palette.neutralQuaternaryAlt,
            borderRadius: '0.1em',
            marginTop: '2%',
            marginLeft: '2%',
            position: 'relative',
        }} ref={chartContainerRef}>
        </div>
    </>)
}

