const Info = () => {
    const { commit } = store();
    const [isCalloutVisible, setIsCalloutVisible] = React.useState<boolean>(false);
    const timeago = () => {
        if (commit.timestamp) {
            const ms = new Date().getTime() - new Date(commit.timestamp).getTime();
            const seconds = Math.floor(ms / 1000);
            const minutes = Math.floor(seconds / 60);
            const hours = Math.floor(minutes / 60);
            const days = Math.floor(hours / 24);
            if (days > 0) return `${days} days ago`;
            if (hours > 0) return `${hours} hours ago`;
            if (minutes > 0) return `${minutes} minutes ago`;
            if (seconds > 0) return `${seconds} seconds ago`;
        }
        return `Loading...`
    }
    return (<>
        {isCalloutVisible && <Callout
            role="dialog"
            gapSpace={0}
            target={`#infobtn`}
            onDismiss={() => setIsCalloutVisible(false)}
            setInitialFocus
        >
            <Stack style={{
                padding: '10px',
                display: 'flex',
                justifyContent: 'center',
                alignItems: 'center',
            }}>
                <Label>
                    Charts and Financial Instruments Icons by <Link href="https://www.tradingview.com/" target="_blank">TradingView</Link>
                </Label>
                <Label>
                    Performance data is not simulated last update was pushed to git on {new Date(commit.timestamp).toLocaleString()}
                </Label>
                <Label>
                    This is not an investment product, just hosting my reports on git 👌
                </Label>
            </Stack>
        </Callout>}

        <div style={{
            width: '202px',
            height: '22px',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            fontWeight: 'bold',
            fontSize: '0.2em',
        }}>
            <Label style={{
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
            }}>
                <DefaultButton style={{
                    fontWeight: 'bold',
                    fontSize: '0.8em',
                    border: 'none',
                }} id="infobtn" onClick={() => setIsCalloutVisible(!isCalloutVisible)} text={`Last Updated ${timeago()}`} iconProps={{ iconName: 'Info' }} />
            </Label>
        </div>
    </>)
}

