const Chart = ({ percentage, data }: any) => {
    const { route, setChart, setChartRef, chart, fetchPrices } = store();
    const chartContainerRef = React.useRef(null as any) as any;
    //const [legend, setLegend] = React.useState(<><Legend market={route.market} /></> as any);
    const [legend, setLegend] = React.useState('' as any);

    const format = {
        price: (price: any) => {
            if (!percentage) return parseFloat(price.toFixed(route.market.precision.price)) + ' ' + currencyFormat(route.market.quote);
            else return parseFloat(price.toFixed(5)) + '%';
        },
        time: (time: any) => {
            return new Date(time).toLocaleString();
        },
        date: (date: any) => {
            return new Date(date).toLocaleDateString();
        },
    }
    const loadData = React.useCallback((chart: any) => {
        let priceSeries: any;
        chart.applyOptions({
            localization: {
                priceFormatter: format.price,
                timeFormatter: format.time,
                dateFormatter: format.date,
            },
        });
        priceSeries = chart.addCandlestickSeries({
            upColor: theme.palette.green,
            downColor: theme.palette.red,
            borderDownColor: theme.palette.redDark,
            borderUpColor: theme.palette.greenDark,
            wickDownColor: theme.palette.redDark,
            wickUpColor: theme.palette.greenDark,
            ticksVisible: true,
        })
        priceSeries.setData(data.map((d: any) => ({
            time: new Date(d.time).getTime(),
            open: d.open,
            high: d.high,
            low: d.low,
            close: d.close,
        })));
        chart.timeScale().scrollToRealTime()
        chart.priceScale('right').applyOptions({
            alignLabels: true,
            autoScale: true,
            entireTextOnly: true,
            ticksVisible: true,
        });
        return priceSeries;
    }, [data, route.market.base, route.market.name, route.market.quote]);

    const toopTipHandler = (chart: any, series: any, label: any) => {


        const toolTipWidth = 80;
        const toolTipHeight = 80;
        const toolTipMargin = 15;

        let toolTip = document.createElement('div') as any;
        toolTip.style = `width: 168px; font-weight:600; height: 130px; position: absolute; display: none; padding: 8px; box-sizing: border-box; font-size: 12px; text-align: left; z-index: 1000; top: 12px; left: 12px; pointer-events: none; border: 1px solid; border-radius: 0.1em;font-family: -apple-system, BlinkMacSystemFont, 'Trebuchet MS', Roboto, Ubuntu, sans-serif; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; background: #ffffff00; color: #000000; border-color: #2962FF; backdrop-filter: blur(10px); -webkit-backdrop-filter: blur(10px);`;
        chartContainerRef.current.appendChild(toolTip);

        chart.subscribeCrosshairMove((param: any) => {
            if (
                param.point === undefined ||
                !param.time ||
                param.point.x < 0 ||
                param.point.x > chartContainerRef.current.clientWidth ||
                param.point.y < 0 ||
                param.point.y > chartContainerRef.current.clientHeight
            ) {
                toolTip.style.display = 'none';
            } else {
                // time will be in the same format that we supplied to setData.
                // thus it will be YYYY-MM-DD
                const dateStr = new Date(param.time).toLocaleString();
                toolTip.style.display = 'block';
                const data = param.seriesData.get(series);
                const price = data;
                toolTip.style.borderColor = price.close >= price.open ? 'green' : 'red';
                toolTip.innerHTML = `
                <div style="display:flex;justify-content:center;align-items:center;color: ${toolTip.style.borderColor}">${label}</div>
                <table style="width:100%;font-size:12px;display:flex;justify-content:center;align-items:center;border-width: 1px;border-style: solid;border-color: #00000050;border-radius: 2px;">
                    <tr style="border-width: 1px;border-style: solid;border-color: #00000050;border-radius: 2px;">
                        <td>High:</td>
                        <td>${format.price(price.high)}</td>
                    </tr>
                    ${price.open > price.close ? `
                <tr style="border-width: 1px;border-style: solid;border-color: #00000050;border-radius: 2px;">
                    <td>Open:</td>
                    <td>${format.price(price.open)}</td>
                </tr>
                <tr style="border-width: 1px;border-style: solid;border-color: #00000050;border-radius: 2px;">
                    <td>Close:</td>
                    <td>${format.price(price.close)}</td>
                </tr>
                ` : `
                <tr style="border-width: 1px;border-style: solid;border-color: #00000050;border-radius: 2px;">
                    <td>Close:</td>
                    <td>${format.price(price.close)}</td>
                </tr>
                <tr style="border-width: 1px;border-style: solid;border-color: #00000050;border-radius: 2px;">
                    <td>Open:</td>
                    <td>${format.price(price.open)}</td>
                </tr>
                `}

                    <tr style="border-width: 1px;border-style: solid;border-color: #00000050;border-radius: 2px;">
                        <td>Low:</td>
                        <td>${format.price(price.low)}</td>
                    </tr>
                </table>
                <div style="display:flex;justify-content:center;align-items:center;color: ${'black'}"> ${dateStr} </div>`;
                const coordinate = series.priceToCoordinate(price.close);
                let shiftedCoordinate = param.point.x - 50;
                if (coordinate === null) {
                    return;
                }
                shiftedCoordinate = Math.max(
                    0,
                    Math.min(chartContainerRef.current.clientWidth - toolTipWidth, shiftedCoordinate)
                );
                const coordinateY =
                    coordinate - toolTipHeight - toolTipMargin > 0
                        ? coordinate - toolTipHeight - toolTipMargin
                        : Math.max(
                            0,
                            Math.min(
                                chartContainerRef.current.clientHeight - toolTipHeight - toolTipMargin,
                                coordinate + toolTipMargin
                            )
                        );
                toolTip.style.left = shiftedCoordinate + 'px';
                toolTip.style.top = coordinateY + 'px';
            }
        });
    }
    const loadLegend = React.useCallback((chart: any, priceSeries: any) => {

        chart.subscribeCrosshairMove((param: any) => {
            let priceFormatted = '' as any;
            if (param.time) {
                priceFormatted = param.seriesData.get(priceSeries);
            }
        });

    }, [route.market.base, route.market.name, route.market.quote]);
    React.useEffect(() => {
        const handleResize = () => {
            _chart.applyOptions({ width: chartContainerRef.current.clientWidth });
        };
        const _chart = createChart(chartContainerRef.current, {
            width: chartContainerRef.current.clientWidth,
            height: 300,
            watermark: {
                visible: true,
                text: `${route.market.name} bid/ask spread on ${route.strategy.name}`,
                fontSize: 24,
                color: theme.palette.neutralQuaternary,
                fontStyle: 'bold',
                horzAlign: 'left',
                vertAlign: 'top',
            },
            grid: {
                vertLines: {
                    color: 'rgba(197, 203, 206, 0)'
                },
                horzLines: {
                    color: 'rgba(197, 203, 206, 0)'
                },
            },
            crosshair: {
                vertLine: {
                    color: theme.palette.neutralDark,
                    width: 1,
                    style: 1,
                    visible: true,
                },
                horzLine: {
                    color: theme.palette.neutralDark,
                    width: 1,
                    style: 1,
                    visible: true,
                },
            },
            timeScale: {
                borderColor: theme.palette.neutralQuaternaryAlt,
            },
            rightPriceScale: {
                borderColor: theme.palette.neutralQuaternaryAlt,
            },
            leftPriceScale: {
                borderColor: theme.palette.neutralQuaternaryAlt,
            },

        });
        const series = loadData(_chart);
        toopTipHandler(_chart, series, `${route.market.name}`);
        loadLegend(_chart, series);
        _chart.timeScale().scrollToRealTime();
        window.addEventListener('resize', handleResize);
        setChart(chart);
        return () => {
            window.removeEventListener('resize', handleResize);
            _chart.remove();
        };
    }, [chartContainerRef.current, route.market]);
    return (<>
        <Label style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
            {route.market ? (<>
                <Legend market={route.market} />
            </>) : 'Select market'}
        </Label>
        <div ref={chartContainerRef} style={{
            position: 'relative',
            marginRight: '-0.4em',
            zIndex: 0,
        }}>
        </div>
    </>)
}

const ChartLoader = () => {
    const { route, minDate, maxDate, isLoading, isLoadingMessage, setPercentage, fetchPrices, gotoTrade, setDate, startDate, endDate, search, setSearch, setStartDate, setEndDate,
        tradesFiltered, trades, filters, setFilters, setTrades, searchedTrades } = store();
    const [content, setContent] = React.useState(<></> as any);
    const Header = () => {
        return (<>
            <div style={{
                width: '100%',
                height: '100%',
            }}>
                <Label style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
                    {route.market ? (<>
                        <Legend market={route.market} />
                    </>) : 'Select market'}
                </Label>
            </div>
        </>)
    }
    const Content = (props: any) => {
        return (<>
            <div style={{
                height: '300px',
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center',
            }}>
                {props.children}
            </div>
        </>)
    }
    const Loader = () => {
        return (<>
            <Header />
            <Content>
                <Spinner
                    label={`Loading ${strings.priceData()}`}
                />
            </Content>
        </>)
    }
    const Result = ({ percentage, data }: any) => {
        return (<Chart percentage={percentage} data={data} />)
    }
    const Error = () => {
        return (<>
            <Header />
            <Content>
                <MessageBar messageBarType={MessageBarType.error} style={{ width: '50%' }}>
                    <Icon iconName="Chart" style={{ marginRight: '0.5em' }} /> Could not load {strings.priceData()}
                </MessageBar>
            </Content >
        </>)
    }
    const Load = React.useCallback(() => {
        setContent(<Loader />);
        fetchPrices().then(({ percentage, prices }: any) => {
            setContent(<Result percentage={percentage} data={prices} />);
        }).catch((err: any) => {
            setContent(<Error />);
        });
    }, [route.market]);
    React.useEffect(() => {
        Load();
    }, [route.market]);
    return (<>
        {content}

        <Stack horizontal tokens={{ childrenGap: 10, }} style={{ display: 'flex', alignItems: 'center', justifyContent: 'center', width: '100%', marginTop: '0.4em' }}>
            <Checkbox label="Percentage (%)" onChange={(e, checked) => {
                setPercentage(checked, () => {
                    Load()
                })
            }} />
            <DatePicker
                value={startDate}
                style={{ width: '150px', marginTop: '0.25em' }}
                placeholder='From date'
                onSelectDate={(date) => {
                    setDate({ start: date }, () => {
                        Load();
                    })
                }}
            //minDate={minDate}
            //maxDate={maxDate}
            />
            <DatePicker
                value={endDate}
                style={{ width: '150px', marginTop: '0.25em' }}
                placeholder='To date'
                onSelectDate={(date) => {
                    setDate({ end: date }, () => {
                        Load()
                    })
                }}
            //minDate={minDate}
            //maxDate={maxDate}
            />
        </Stack>
    </>)
}

const Menu = () => {
    const { menu, strategies, setRoute } = store();

    const parseMenu = (arr: any) => ({
        items: arr.reduce((acc: any, item: any) => {
            item.markets.forEach((market: any) => {
                acc.push({
                    name: market.name,
                    strategy: item.name,
                    market: item.market,
                    fee: item.fee,
                    onClick: () => setRoute({
                        strategy: {
                            name: item.name,
                            market: item.market,
                            fee: item.fee,
                        },
                        market,
                    } as any),
                })
            })
            return acc;
        }, []),
        groups: (() => {
            let _index = 0;
            return arr.map((item: any, i: any) => {
                if (i !== 0) _index += arr[i - 1].markets.length;
                return ({
                    name: item.name,
                    key: item.name + '-' + item.market,
                    startIndex: _index,
                    count: item.markets.length,
                    isCollapsed: true,
                    level: 0,
                })
            })
        })(),
    });
    return (<>
        <div style={{
            width: '200px',
        }}>
            <DetailsList items={menu.items} styles={{
                root: {
                    width: '200px',
                    height: '93vh',
                    boxSizing: 'border-box',
                    border: '1px solid #eee',
                    overflowY: 'auto',
                    marginBottom: '0.4em'
                }
            }}
                compact
                selectionMode={SelectionMode.none}
                groups={menu.groups}
                columns={[
                    {
                        key: 'column1',
                        name: 'Menu',
                        fieldName: 'name',
                        minWidth: 100,
                        maxWidth: 200,
                        onRender: (item: any) => {
                            return (<>
                                <span onClick={item.onClick} style={{
                                    cursor: 'pointer',
                                }}>
                                    {item.name}
                                </span>
                            </>)
                        }
                    }
                ]}
            />
            <Info />
        </div>
    </>)
}

