const groupNameStyle = {
    padding: "8px 16px",
    fontSize: "10px",
    textTransform: "uppercase" as const,
    opacity: 0.5,
    zIndex: 1001,
};

const ResultItem = React.forwardRef(
    (
        {
            action,
            active,
            currentRootActionId,
        }: any,
        ref: any
    ) => {
        const ancestors = React.useMemo(() => {
            if (!currentRootActionId) return action.ancestors;
            const index = action.ancestors.findIndex(
                (ancestor: any) => ancestor.id === currentRootActionId
            );
            // +1 removes the currentRootAction; e.g.
            // if we are on the "Set theme" parent action,
            // the UI should not display "Set theme… > Dark"
            // but rather just "Dark"
            return action.ancestors.slice(index + 1);
        }, [action.ancestors, currentRootActionId]);

        return (
            <div
                ref={ref}
                style={{
                    padding: "12px 16px",
                    background: active ? theme.palette.neutralLight : "none",
                    borderLeft: `2px solid ${active ? theme.palette.themePrimary : "transparent"}`,
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "space-between",
                    cursor: "pointer",
                }}
            >
                <div
                    style={{
                        display: "flex",
                        gap: "8px",
                        alignItems: "center",
                        fontSize: 14,
                    }}
                >
                    {action.icon && action.icon}
                    <div style={{ display: "flex", flexDirection: "column" }}>
                        <div>
                            {ancestors.length > 0 &&
                                ancestors.map((ancestor: any) => (
                                    <React.Fragment key={ancestor.id}>
                                        <span
                                            style={{
                                                opacity: 0.5,
                                                marginRight: 8,
                                            }}
                                        >
                                            {ancestor.name}
                                        </span>
                                        <span
                                            style={{
                                                marginRight: 8,
                                            }}
                                        >
                                            &rsaquo;
                                        </span>
                                    </React.Fragment>
                                ))}
                            <span>{action.name}</span>
                        </div>
                        {action.subtitle && (
                            <span style={{ fontSize: 12 }}>{action.subtitle}</span>
                        )}
                    </div>
                </div>
                {
                    action.shortcut?.length ? (
                        <div
                            aria-hidden
                            style={{ display: "grid", gridAutoFlow: "column", gap: "4px" }}
                        >
                            {action.shortcut.map((sc: any) => (
                                <kbd
                                    key={sc}
                                    style={{
                                        padding: "4px 6px",
                                        background: "rgba(0 0 0 / .1)",
                                        borderRadius: "4px",
                                        fontSize: 14,
                                    }}
                                >
                                    {sc}
                                </kbd>
                            ))}
                        </div>
                    ) : null
                }
            </div >
        );
    }
);
